import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'blogType'
})
export class BlogTypePipe implements PipeTransform {

  transform(type: string): string {
    let tmp = ''
    switch (type) {
      case 'note':
        tmp = '';
        break;
      case 'document':
        tmp = '';
        break;
      case 'skill':
        tmp = '';
        break;
      case 'recommand':
        tmp = '';
        break;
      case 'innovationMethod':
        tmp = '';
        break;
      case 'innovationTech':
        tmp = '';
        break;
      case 'innovationThing':
        tmp = '';
        break;
      case 'innovation':
        tmp = '';
        break;
      case 'translation':
        tmp = '';
        break;
      default:
        tmp = ''
    }
    return tmp;
  }

}
